<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/ftp/list"  @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
        <li>带 * 为必填项</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px">
        <el-form-item label="FTP名称" prop="ftpName" :error='errorMessage.ftpName'>
          <el-input v-model="formData.ftpName"></el-input>
        </el-form-item>
        <el-form-item label="服务器IP" prop="ip" :error='errorMessage.ip'>
          <el-input v-model="formData.ip"></el-input>
        </el-form-item>
        <el-form-item label="端口号" prop="port" :error='errorMessage.port'>
          <el-input maxlength="10" v-model.number="formData.port" ></el-input>
        </el-form-item>
        <el-form-item label="登录名" prop="username" :error='errorMessage.username'>
          <el-input v-model="formData.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password" :error='errorMessage.password'>
          <el-input v-model="formData.password" type="password"></el-input>
        </el-form-item>
        <el-form-item label="编码" prop="encoding" :error='errorMessage.encoding'>
          <el-input v-model="formData.encoding"></el-input>
        </el-form-item>
        <el-form-item label="资源访问域名" prop="url" :error='errorMessage.url'>
          <el-input v-model="formData.url"></el-input>
          <div class="gray">填写带访问协议的访问域名</div>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button  @click="save(false)" v-color >保存并新增</cms-button>
        <cms-button @click="save(true)"  v-color >保存</cms-button>
        <cms-button type='rest' @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { ftpSave } from '@/api/setting/ftp.js'

var defaultData = {
  'ftpName': '',
  'ip': '',
  'port': '',
  'username': '',
  'password': '',
  'encoding': 'UTF-8',
  'url': ''
}
export default {
  name: 'ftpSave',
  mixins: [form],
  data() {
    var isIp = (rules, value, callback) => {
      const patrnIp = /^(1\d{2}|2[0-4]\d|25[0-5]|[1-9]\d|[1-9])(\.(1\d{2}|2[0-4]\d|25[0-5]|[1-9]\d|\d)){3}$/
      if (!patrnIp.test(value)) {
        callback(new Error('ip地址格式不正确'))
      } else {
        callback()
      }
    }

    return {
      formData: Object.assign({}, defaultData),
      saveRules: {// 新增规则
        ftpName: [rules.required('请填写FTP名称'), rules.len(1, 25)],
        ip: [rules.required('请填写服务器ip'), rules.len(1, 30), rules.custom(isIp)],
        port: [rules.required('请填写端口号'), rules.number()],
        username: [rules.required('请填写登录名'), rules.len(1, 15)],
        password: [rules.required('请填写密码'), rules.len(6, 16)],
        encoding: [rules.required('请填写编码'), rules.len(1, 20)],
        url: [rules.required('请填写资源访问域名'), rules.len(1, 30)]
      }
    }
  },
  methods: {
    save(state) { // 添加
      this.saveForm(ftpSave, this.formData, state, '/ftp/list', 'list')
    }
  },
  activated() {
    this.autoFocus()
    if (this.$route.query.type === 'save') {
      this.formData = Object.assign({}, defaultData)
    }
  }
}
</script>




